.buttons {
    position: absolute;
    bottom: 100px;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.buttons button {
    border: none;
    outline: none;
    margin-top: 10px;
    margin-left: 20px;
    margin-right: 20px;
    display: inline-block;
    width: 60px;
    height: 40px;
    color: white;
    background-color: orange;
}

* {
    margin: 0;
    padding: 0;
}

html,
body {
    overflow: hidden;
}